import React from "react";
import {Box, Button, FormControl, InputLabel, MenuItem, Select} from "@mui/material";
import {styled} from "@mui/material/styles";

const StyledTruncateOptionButton = styled(Button)({
    fontSize: "1rem",
    border: "none",
    cursor: "pointer",
});

function TruncateOption(props) {
    const [value, setValue] = React.useState("ONE");

    const handleChange = (event) => {
        setValue(event.target.value);
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        if (props.onSubmit) {
            props.onSubmit(value);
        }
    };

    return (
        <Box display="flex" alignItems="center" justifyContent="center" marginBottom="3rem">
            <Box flexGrow={1} maxWidth="200px">
                <FormControl fullWidth>
                    <InputLabel shrink></InputLabel>
                    <Select value={value} onChange={handleChange} displayEmpty>
                        <MenuItem value="ONE" style={{justifyContent: "right"}}>1</MenuItem>
                        <MenuItem value="TEN" style={{justifyContent: "right"}}>10</MenuItem>
                        <MenuItem value="HUNDRED" style={{justifyContent: "right"}}>100</MenuItem>
                        <MenuItem value="THOUSAND" style={{justifyContent: "right"}}>1000</MenuItem>
                    </Select>
                </FormControl>
            </Box>
            <Box flexGrow={1} maxWidth="150px">
                <Box textAlign="center" margin="0.5rem"> </Box>
            </Box>
            <Box flexGrow={1} maxWidth="150px">
                <StyledTruncateOptionButton fullWidth variant="contained" color="primary" onClick={handleSubmit}>
                    
                </StyledTruncateOptionButton>
            </Box>
        </Box>
    );
}
export default TruncateOption;