import React, {useState} from 'react';
import {
    Badge,
    Button,
    Checkbox,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    FormControlLabel,
    IconButton,
    MenuItem,
    Select,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    TextField,
    Typography,
} from '@mui/material';
import {Delete, Edit} from '@mui/icons-material';


const SPLIT_OPTIONS = [
    {value: 'DEFAULT', label: '', description: '   '},
    {value: 'DONE', label: ' ', description: '   /   '},
    {value: 'RATE_CHANGE', label: ' ', description: '      / 70%   0.7 '},
    {value: 'VALUE_CHANGE', label: ' ', description: '     / 5000    -5000 '},
    {value: 'SET_DIVISION', label: ' ', description: '       '},
    {value: 'DRINK_SEPARATE', label: ' ', description: '     /    ,   '},
    {value: 'TREAT', label: '', description: '   '},
];

function ExpenseInput(props) {
    const [expenseInputs, setExpenseInputs] = useState(props.expenseInputs || []);
    const onChangeExpense = props.onChangeExpense;
    const members = props.members;
    const [open, setOpen] = useState(false);
    const [editIndex, setEditIndex] = useState(-1);
    const [splitOptionDescription, setSplitOptionDescription] = useState(SPLIT_OPTIONS.find((option) => option.value === 'DEFAULT').description || []);

    const getDefaultExpense = () => ({
        description: '',
        amount: '',
        payer: '',
        participants: members,
        splitOption: 'DEFAULT',
        splitDetails: {},
        drinkAmount: '',
    });

    const [expense, setExpense] = useState(getDefaultExpense());

    const handleChange = (e, field) => {
        const value = e.target.value;
        if (field === 'splitOption' && expense.splitOption !== value) {
            setExpense({...expense, [field]: value, splitDetails: {}});
        } else {
            setExpense({...expense, [field]: value});
        }
        if (field === 'splitOption') {
            const option = SPLIT_OPTIONS.find((option) => option.value === value);
            setSplitOptionDescription(option ? option.description : '');
        }
    };

    const handleToggleParticipant = (participant) => {
        const updatedParticipants = [...expense.participants];
        if (updatedParticipants.includes(participant)) {
            const index = updatedParticipants.indexOf(participant);
            updatedParticipants.splice(index, 1);
        } else {
            updatedParticipants.push(participant);
        }

        setExpense({...expense, participants: updatedParticipants});
    };


    const handleSave = () => {
        if (editIndex === -1) {
            onChangeExpense([...expenseInputs, expense]);
            setExpenseInputs([...expenseInputs, expense]);
        } else {
            const updatedExpenses = [...expenseInputs];
            updatedExpenses[editIndex] = expense;
            onChangeExpense(updatedExpenses);
            setExpenseInputs(updatedExpenses);
            setEditIndex(-1);
        }
        handleClose();
    };

    const handleEdit = (index) => {
        setEditIndex(index);
        setExpense(expenseInputs[index]);
        setOpen(true);
    };

    const handleDelete = (index) => {
        const updatedExpenses = [...expenseInputs];
        updatedExpenses.splice(index, 1);
        onChangeExpense(updatedExpenses);
        setExpenseInputs(updatedExpenses);
    };

    const handleClose = () => {
        setOpen(false);
        setExpense(getDefaultExpense());
    };

    const handleOpen = () => {
        setExpense(getDefaultExpense());
        setOpen(true);
    };

    const getParticipantsDisplay = (participants) => {
        const displayArray = participants.slice(0, 5);
        let displayText = displayArray.join(', ');

        if (participants.length > 5) {
            displayText = (
                <Badge badgeContent={`+${participants.length - 5}`} color="primary">
                    {displayText}
                </Badge>
            );
        }
        return displayText;
    };

    return (
        <div>
            <Typography variant="h6" align="center" gutterBottom>
                <b>  ( {expenseInputs.length})</b>
            </Typography>
            <TableContainer style={{textAlign: 'center', marginLeft: "0.3rem", marginRight: "0.3rem"}}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell style={{textAlign: 'center', padding: "0.2rem 0.4rem"}}> </TableCell>
                            <TableCell style={{
                                whiteSpace: 'nowrap',
                                textAlign: 'center',
                                padding: "0.2rem 0.5rem"
                            }}></TableCell>
                            <TableCell style={{
                                textAlign: 'center',
                                padding: "0.2rem 0.4rem",
                                wordWrap: 'break-word',
                                whiteSpace: 'pre-line'
                            }}>
                                <div style={{display: 'flex', flexDirection: 'column'}}>
                                    
                                    <span style={{fontWeight: 'normal'}}></span>
                                </div>
                            </TableCell>

                            <TableCell style={{
                                textAlign: 'center',
                                padding: "0.2rem 0.4rem",
                                wordWrap: 'break-word',
                                whiteSpace: 'pre-line'
                            }}>
                                <div style={{display: 'flex', flexDirection: 'column'}}>
                                    
                                    <span style={{fontWeight: 'normal'}}></span>
                                </div>
                            </TableCell>
                            <TableCell style={{textAlign: 'center', padding: "0.3rem 0.4rem"}}> </TableCell>
                            <TableCell style={{padding: '0rem'}}></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {expenseInputs.map((expense, index) => (
                            <TableRow key={index}>
                                <TableCell style={{
                                    minWidth: '2.3rem',
                                    textAlign: 'center',
                                    paddingLeft: "0.3rem",
                                    paddingRight: "0.3rem"
                                }}>{expense.description}</TableCell>
                                <TableCell style={{
                                    minWidth: '3.3rem',
                                    textAlign: 'center',
                                    paddingLeft: "0.3rem",
                                    paddingRight: "0.3rem"
                                }}>{expense.amount}</TableCell>
                                <TableCell style={{
                                    minWidth: '3.3rem',
                                    textAlign: 'center',
                                    paddingLeft: "0.3rem",
                                    paddingRight: "0.3rem"
                                }}>{expense.payer}</TableCell>
                                <TableCell style={{wordWrap: 'break-word'}}>
                                    {getParticipantsDisplay(expense.participants)}
                                </TableCell>
                                <TableCell style={{
                                    minWidth: '2.3rem',
                                    textAlign: 'center',
                                    paddingLeft: "0.2rem",
                                    paddingRight: "0.2rem"
                                }}>
                                    {SPLIT_OPTIONS.find((option) => option.value === expense.splitOption).label}
                                </TableCell>
                                <TableCell style={{padding: '0', margin: '0', textAlign: 'center'}}>
                                    <IconButton onClick={() => handleEdit(index)}>
                                        <Edit/>
                                    </IconButton>
                                    <IconButton onClick={() => handleDelete(index)}>
                                        <Delete/>
                                    </IconButton>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <div style={{display: 'flex', justifyContent: 'center', marginTop: '2rem', marginBottom: '3rem'}}>
                <Button variant="contained" onClick={handleOpen}>
                     
                </Button>
            </div>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle style={{
                    textAlign: 'center',
                    paddingTop: '1.5rem',
                    paddingBottom: '1.5rem'
                }}><b>{editIndex === -1 ? ' ' : ' '}</b></DialogTitle>
                <DialogContent>
                    <div style={{display: 'flex', alignItems: 'center', marginBottom: '0.8rem'}}>
                        <Typography variant="body1"
                                    style={{textAlign: 'center', marginRight: '0.7rem', lineHeight: '1.2rem'}}>
                            <br/>()</Typography>
                        <TextField type='text' inputProps={{style: {padding: '0.75rem 1rem'},}}
                                   style={{width: '8rem', marginLeft: 'auto'}} value={expense.description}
                                   onChange={(e) => handleChange(e, 'description')}/>
                    </div>
                    <div style={{display: 'flex', alignItems: 'center', marginBottom: '0.8rem'}}>
                        <Typography variant="body1" style={{marginLeft: '1rem'}}></Typography>
                        <TextField type="number" inputProps={{min: 0, max: 99999999, style: {padding: '0.75rem 1rem'},}}
                                   style={{width: '8rem', marginLeft: 'auto'}} value={expense.amount}
                                   onChange={(e) => handleChange(e, 'amount')}/>
                    </div>
                    <div style={{display: 'flex', alignItems: 'center', marginBottom: '0.8rem'}}>
                        <Typography variant="body1" style={{marginLeft: '0.7rem'}}></Typography>
                        <Select style={{width: '8rem', marginLeft: 'auto', padding: '0.2rem 0.1rem'}} size='small'
                                value={expense.payer} onChange={(e) => handleChange(e, 'payer')}>
                            {members.map((member, index) => (
                                <MenuItem key={index} value={member}>
                                    {member}
                                </MenuItem>
                            ))}
                        </Select>
                    </div>
                    <Typography style={{textAlign: 'center', padding: '0.4rem 0rem'}} variant="subtitle1" gutterBottom>
                         
                    </Typography>
                    <div style={{textAlign: 'center', paddingBottom: '2rem'}}>
                        {members.map((member, index) => (
                            <Button style={{marginRight: '0.1rem', marginBottom: '0.2rem'}}
                                    key={index}
                                    variant={expense.participants.includes(member) ? 'contained' : 'outlined'}
                                    onClick={() => handleToggleParticipant(member)}
                            >
                                {member}
                            </Button>
                        ))}
                    </div>

                    <div style={{display: 'flex', alignItems: 'center', marginBottom: '0.8rem'}}>
                        <Typography variant="body1" style={{marginLeft: '0.2rem'}}> </Typography>
                        <Select style={{width: '8rem', marginLeft: 'auto', padding: '0.2rem 0.1rem'}} size='small'
                                value={expense.splitOption} onChange={(e) => handleChange(e, 'splitOption')}>
                            {SPLIT_OPTIONS.map((option, index) => (
                                <MenuItem key={index} value={option.value}>
                                    {option.label}
                                </MenuItem>
                            ))}
                        </Select>
                    </div>
                    {splitOptionDescription && (
                        <Typography variant="body2" style={{marginBottom: '1.5rem'}}>
                            {splitOptionDescription.split('/').map((part, index) => (
                                <span key={index}>
                                    {part}
                                    {index !== splitOptionDescription.split('/').length - 1 && <br/>}
                                </span>
                            ))}
                        </Typography>
                    )}

                    {expense.splitOption === 'RATE_CHANGE' &&
                        <div style={{
                            display: 'grid', alignItems: 'center', marginBottom: '0.8rem'
                        }}>
                            {expense.participants.map((participant, index) => (
                                <div key={index} style={{display: 'flex', alignItems: 'center'}}>
                                    <Typography variant="body1" component="span" style={{whiteSpace: 'nowrap'}}>
                                        {participant}
                                    </Typography>
                                    <TextField
                                        style={{width: '6rem', padding: '0.2rem', marginLeft: 'auto'}}
                                        type="number"
                                        inputProps={{min: 0.1, max: 10}}
                                        value={expense.splitDetails[participant] || ''}
                                        size="small"
                                        onChange={(e) => {
                                            const updatedSplitDetails = {
                                                ...expense.splitDetails,
                                                [participant]: e.target.value,
                                            };
                                            setExpense({
                                                ...expense,
                                                splitDetails: updatedSplitDetails,
                                            });
                                        }}
                                    />
                                </div>
                            ))}
                        </div>
                    }

                    {expense.splitOption === 'VALUE_CHANGE' &&
                        <div style={{
                            display: 'grid', alignItems: 'center', marginBottom: '0.8rem'
                        }}>
                            {expense.participants.map((participant, index) => (
                                <div key={index} style={{display: 'flex', alignItems: 'center'}}>
                                    <Typography variant="body1" component="span" style={{whiteSpace: 'nowrap'}}>
                                        {participant}
                                    </Typography>
                                    <TextField
                                        style={{width: '6rem', padding: '0.2rem', marginLeft: 'auto'}}
                                        type="number"
                                        inputProps={{min: -1000000, max: 1000000}}
                                        value={expense.splitDetails[participant] || ''}
                                        size="small"
                                        onChange={(e) => {
                                            const updatedSplitDetails = {
                                                ...expense.splitDetails,
                                                [participant]: e.target.value,
                                            };
                                            setExpense({
                                                ...expense,
                                                splitDetails: updatedSplitDetails,
                                            });
                                        }}
                                    />
                                </div>
                            ))}
                        </div>
                    }

                    {expense.splitOption === 'SET_DIVISION' &&
                        <div style={{
                            display: 'grid', alignItems: 'center', marginBottom: '0.8rem'
                        }}>
                            {expense.participants.map((participant, index) => (
                                <div key={index} style={{display: 'flex', alignItems: 'center'}}>
                                    <Typography variant="body1" component="span" style={{whiteSpace: 'nowrap'}}>
                                        {participant}
                                    </Typography>
                                    <TextField
                                        style={{width: '6rem', padding: '0.2rem', marginLeft: 'auto'}}
                                        type="number"
                                        inputProps={{min: 0, max: 1000000}}
                                        value={expense.splitDetails[participant] || ''}
                                        size="small"
                                        onChange={(e) => {
                                            const updatedSplitDetails = {
                                                ...expense.splitDetails,
                                                [participant]: e.target.value,
                                            };
                                            setExpense({
                                                ...expense,
                                                splitDetails: updatedSplitDetails,
                                            });
                                        }}
                                    />
                                </div>
                            ))}
                        </div>
                    }

                    {expense.splitOption === 'DRINK_SEPARATE' && (
                        <div>
                            {expense.participants.map((participant, index) => (
                                <div key={index}>
                                    <FormControlLabel
                                        control={
                                            <Checkbox
                                                checked={!!expense.splitDetails[participant]}
                                                onChange={() => {
                                                    const updatedSplitDetails = {
                                                        ...expense.splitDetails,
                                                        [participant]: 1,
                                                    };
                                                    setExpense({
                                                        ...expense,
                                                        splitDetails: updatedSplitDetails,
                                                    });
                                                }}
                                            />
                                        }
                                        label={participant}
                                    />
                                </div>
                            ))}
                            <div style={{display: 'flex', alignItems: 'center', marginTop: '0.8rem'}}>
                                <Typography variant="body1" style={{marginLeft: '0.2rem'}}> </Typography>
                                <TextField type="number"
                                           inputProps={{min: 0, max: 99999999, style: {padding: '0.75rem 1rem'},}}
                                           style={{width: '8rem', marginLeft: 'auto'}} value={expense.drinkAmount}
                                           onChange={(e) => handleChange(e, 'drinkAmount')}/>
                            </div>
                        </div>
                    )}

                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}></Button>
                    <Button onClick={handleSave}>{editIndex === -1 ? '' : ''}</Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default ExpenseInput;